import { StationList, TrainType, TrainTypeMap } from "@/types";
import { createStore, useStore as baseUseStore, Store } from "vuex";
import { InjectionKey } from "vue";
import { chartJsData, DiagramData } from "@/types/diagram";
import rfdc from "rfdc";
import { getters } from "./getters";
import { mutations } from "./mutations";
import { actions } from "./actions";
import { MyCommit } from "./mutations.type";
import { MyDispatch } from "./actions.type";
import { MyGetters } from "./getters.type";

const clonedeep = rfdc({ circles: true });

export interface State {
  stationList: StationList;
  trainTypes: TrainTypeMap;
  diagramData: chartJsData;
  showingTrainId: number;
  nextTrainId: number;
  __chartRefresh: () => void;
  __history?: { stack: State[]; nowIndex: number };
  __saveId?: number;
  inputtingPreviewDiagramDatas: DiagramData[];
  isShowPreview: boolean;
}

// getters
export interface MyStore extends Store<State> {
  getters: MyGetters;
  commit: MyCommit;
  dispatch: MyDispatch;
}

export const key: InjectionKey<Store<State>> = Symbol();

export const initialState: State = {
  stationList: {
    stations: [],
    startingStationId: 0,
    endingStationId: 0,
  },
  trainTypes: new Map<number, TrainType>([]),
  diagramData: {
    labels: [],
    datasets: [],
  },
  showingTrainId: 0,
  nextTrainId: 1,
  __chartRefresh: () => void 0,
  __saveId: 1,
  inputtingPreviewDiagramDatas: [],
  isShowPreview: true,
};

