export default createStore<State>({
  state: Object.assign(clonedeep(initialState), {
    __history: {
      stack: [],
      nowIndex: 0,
    },
  }),
  getters,
  mutations,
  actions: actions,
  modules: {},
});

export const useStore = (): MyStore => {
  return baseUseStore(key) as MyStore;
};
